module.exports = "<template>\n  <TableFor\n    class=\"table table-hover \n      table-striped table-bordered\"\n    :records=\"records\">\n    <template slot-scope=\"{ record }\">\n      <td name=\"name\">\n        {{ record.first_name }}\n        {{ record.last_name }}\n      </td>\n      <td name=\"printed_name\"\n        :content=\"`${record.last_name}, \n                   ${record.first_name}`\"></td>\n      <td name=\"email\"></td>\n    </template>\n  </TableFor>\n</template>\n\n<script>\nimport samplePeopleRecords from '../../people.json'\n\nexport default {\n  data() {\n    return {\n      records: samplePeopleRecords.slice(0, 10)\n    }\n  }\n}\n</script>\n"